<template>
  <el-row>
    <el-col :span="12" :offset="5">
      <el-form ref="staffShow" :model="staffShow" :rules="rules" label-width="80px">
        <el-form-item label="员工ID">
          <el-input v-model="staffShow.staffID" disabled></el-input>
        </el-form-item>
        <el-form-item label="部门">
          <el-select v-model="staff.depID" :placeholder="staffShow.depName">
            <el-option
              v-for="item in dep"
              :key="item.depID"
              :label="item.depName"
              :value="item.depID"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="岗位">
          <el-select v-model="staff.postID" :placeholder="staffShow.postName">
            <el-option
              v-for="item in post"
              :key="item.postID"
              :label="item.postName"
              :value="item.postID"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="员工姓名" prop="staffName">
          <el-input v-model="staffShow.staffName"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio-group v-model="staffShow.sex">
            <el-radio label="男"></el-radio>
            <el-radio label="女"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="年龄" prop="age">
          <el-input v-model="staffShow.age"></el-input>
        </el-form-item>
        <el-form-item label="电话号码" prop="phoneNum">
          <el-input v-model="staffShow.phoneNum"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="staffShow.email"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="button" @click="Put('staffShow')">确认修改</el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import Qs from 'qs'

export default {
  data() {
    return {
      dep: [],
      post: [],
      staff: {},
      staffShow: {},
      rules: {
        staffName: [
          { required: true, message: "请输入你的姓名", trigger: "blur" },
          { min: 3, max: 8, message: "长度在 3 到 8 个字符", trigger: "blur" }
        ],
        age: [{ required: true, message: "请输入年龄", trigger: "blur" }],
        phoneNum: [
          { required: true, message: "请输入电话号码", trigger: "blur" }
        ],
        email: [{ required: true, message: "请输入邮箱", trigger: "blur" }]
      }
    };
  },
  methods: {
    Put(staffShow) {
      this.$refs[staffShow].validate(valid => {
        if (valid) {
          this.staff.staffName = this.staffShow.staffName;
          this.staff.sex = this.staffShow.sex;
          this.staff.age = this.staffShow.age;
          this.staff.phoneNum = this.staffShow.phoneNum;
          this.staff.email = this.staffShow.email;
          sessionStorage.setItem("depID", this.staff.depID);

          console.log("put对象：" + JSON.stringify(this.staff));
          var api = "http://localhost:8888/staff/detail/" + this.staff.staffID;
          this.axios
            .put(api, Qs.stringify(this.staff))
            .then(response => {
              console.log(response);
              if (response.data == 1) {
                this.$message({
                  showClose: true,
                  message: "操作成功",
                  type: "success"
                });
                this.$router.push({name:"staffList",params:{ current: 1 , size: 5}});
              }
            })
            .catch(error => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "操作失败，请联系维护人员",
                type: "error"
              });
            });
        } else {
          this.$message({
            showClose: true,
            message: "请检查数据是否填写正确！",
            type: "error"
          });
        }
      });
    },
    getShowData() {
      var api =
        "http://localhost:8888/staff/detail/" + this.$route.params.staffID;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.staffShow = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getStaffData() {
      var api =
        "http://localhost:8888/staff/" + this.$route.params.staffID;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.staff = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getDepData() {
      var api = "http://localhost:8888/department/all";
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.dep = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getPostData() {
      var api = "http://localhost:8888/post/all";
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.post = response.data;
        })
        .catch(error => {
          console.log(error);
        });
    }
  },
  created() {
    this.getShowData();
    this.getStaffData();
    this.getDepData();
    this.getPostData();
  }
};
</script>

<style lang="scss" scoped>
.button {
  display: block;
  margin: 0 auto;
  width: 150px;
}
</style>
